@font-face {
    font-family: 'La Belle Aurora';
    src: url('../../assets/fonts/LaBelleAurore-Regular.ttf') format('ttf');
}

@font-face {
    font-family: 'SourceSerifPro';
    src: url('../../assets/fonts/SourceSerifPro-Bold.ttf') format('ttf');
}

.container {
    background: url('../../assets/images/background.jpg') center center no-repeat;
    background-attachment: fixed;
    max-width: 95%;
    min-height: 100vmin;
    background-size: cover;
    background-repeat: no-repeat;
    padding: 5%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.triangle {
    text-align: center;
    min-width: 150px;
    margin-top: 10%;
    margin-bottom: 10%;
    padding: 7%;
    border-radius: 10px;
    background-color: rgb(121, 128, 133, 0.5);
    filter: drop-shadow(10px 10px 5px #ffffff);
    color: rgb(2, 19, 54);
}

.triangle div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 5%;
    text-shadow: rgb(121, 128, 133) 1px 0 10px;
}

.triangle h1 {
    margin-top: 2%;
    display: flex;
    flex-direction: column;
    font-family: 'SourceSerifPro';
    font-weight: 900;
    animation: anim-lineUp 5s forwards;
}

.download {
    display: inline;
    width: fit-content;
    margin-top: 3%;
    padding: 3%;
    border: none;
    border-radius: 10px;
    background-color: rgb(2, 19, 54);
    color: #ffffff;
    animation: anim-lineUp 15s forwards, shine 5s infinite;
    transition: all 0.5s linear;

}

.download:hover {
    cursor: pointer;
    position: relative;
    z-index: 2;
    pointer-events: auto;
    background-color: rgb(66, 94, 121);
    color: rgb(2, 19, 54);
    text-shadow: rgb(54, 52, 52) 1px 0 10px;
}

.download a {
    all: unset;
}

@keyframes anim-lineUp {
    0% {
        opacity: 0;
        transform: translateY(80%);
    }

    20% {
        opacity: 0;
    }

    50% {
        opacity: 1;
        transform: translateY(0%);
    }

    100% {
        opacity: 1;
        transform: translateY(0%);
    }
}

@keyframes shine {
    0% {
        color: #abb4d6;
        text-shadow: none;
    }

    100% {
        color: #140c3a;
        text-shadow: 0 0 0.6rem #77bdff, 0 0 1.5rem #1b64b8,
            -0.2rem 0.1rem 1rem #b7c7ff, 0.2rem 0.1rem 1rem #55659e,
            0 -0.5rem 2rem #848484, 0 0.5rem 3rem #62699c;
    }
}